import React, { useState, useEffect } from "react";
import GridTable from "@nadavshaar/react-grid-table";
import { ReactGridTable } from '@nadavshaar/react-grid-table';
import data from "./data.json"; // Import your data file here
import getColumns from "./getColumns.js";
import "./styles.css";

const SyncTable = () => {
  const [rowsData, setRowsData] = useState([]);
  const [isLoading, setLoading] = useState(true); // Set initial loading to true
  const [isFirstEditableCell, setFirstEditableCell] = useState(false); // Set initial value for isFirstEditableCell

  useEffect(() => {
    // Simulate data loading delay
    setTimeout(() => {
      setRowsData(data.result.value[0]); // Set the data from data.json
      setLoading(false); // Set loading to false when data is loaded
    }, 1500);
  }, []);
  

  return (
    <div className="App">
      <GridTable
        columns={getColumns({ setRowsData, isFirstEditableCell })}
        rows={rowsData}
        isLoading={isLoading}
        onRowClick={({ rowIndex, data, column, isEdit, event }, tableManager) =>
          !isEdit &&
          tableManager.rowSelectionApi.getIsRowSelectable(data.id) &&
          tableManager.rowSelectionApi.toggleRowSelection(data.id)
        }
      />
    </div>
  );
};

export default SyncTable;
